import Codes from '@/components/Codes';

export default {
  data () {
    return {
      paramsHeader: [{
        key: 'prop',
        title: '',
        sticky: 'left',
        render: (h, ctx) => h('i', {
          class: `${ctx.data.event? 'vsm-red': (ctx.data.slot? '': 'vsm-blue')} vsm-reverse`
        }, ctx.data.prop)
      }, {
        key: 'desc',
        title: ''
      }, {
        key: 'type',
        title: '',
        render: (h, ctx) => h('span', {
          class: 'param-type'
        }, ctx.data.type)
      }, {
        key: 'options',
        title: ''
      }, {
        key: 'default',
        title: '',
        sticky: 'right'
      }],
      colorOptions: [
        { label: '', value: '' },
        { label: '', value: 'black' },
        { label: '', value: 'white' },
        { label: '', value: 'red' },
        { label: '', value: 'blue' },
        { label: '', value: 'green' },
        { label: '', value: 'yellow' },
      ]
    }
  },
  components: {
    Codes,
  }
}